import {
    Panel,
    Button,
    Group,
    Avatar,
    PanelHeader,
    HeaderButton,
    Cell,
    IOS,
    Spinner
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';
import moment from 'moment';

import PropTypes from 'prop-types';

import {Friends, Users} from '../api';

class VueFriendsLastActivity extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            usersInfo: [],
            isSpinner: true
        };
    }

    componentDidMount () {
        this.getFriends();
    }

    getFriends () {
        let countFriendsInterval = 100;
        let offsetFriendsInterval = 0;

        this.friendsInterval = setInterval(() => {

            Friends.getFriends({
                count: countFriendsInterval,
                fields: 'nickname, domain, sex, bdate, city, country, timezone, photo_50, photo_100, photo_200_orig, has_mobile, contacts, education, online, relation, last_seen, status, can_write_private_message, can_see_all_posts, can_post, universities',
                offset: offsetFriendsInterval,
                access_token: this.props.access_token,
            })
                .then(res => {

                    if(!res.data.response.items.length) {
                        clearInterval(this.friendsInterval);
                    } else {
                        offsetFriendsInterval += countFriendsInterval;
                        const frendsItems = res.data.response.items.sort((a, b) => {
                            if (!a.last_seen || !b.last_seen ) return 0;

                            if (a.last_seen.time < b.last_seen.time) {
                                return -1;
                            }
                            if (a.last_seen.time > b.last_seen.time) {
                                return 1;
                            }
                            return 0;
                        });
                        console.log(frendsItems);
                        this.setState(state => {
                            state.usersInfo.push(...frendsItems);
                            state.isSpinner = false;
                        });
                        this.forceUpdate();
                    }
                })
                .catch(e => console.log('ERR GET Friends', e));

        }, 2000);
    }

    goInstagram(e, domen) {
        e.stopPropagation();
        return window.open(`https://www.instagram.com/${domen}`);
    }

    prev (e) {
        clearInterval(this.friendsInterval);
        this.props.go(e);
    }

    render () {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.prev.bind(this)} data-to="home">
                        {this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >
                    Друзья
                </PanelHeader>
                <Group title={`Последняя активность друзей ${this.state.usersInfo.length}`}>
                    {
                        this.state.isSpinner &&
                        <Cell size="l">
                            <Spinner size="large" style={{margin: '150px 0'}}/>
                        </Cell>
                    }
                    {
                        this.state.usersInfo &&
                        this.state.usersInfo.map((friend, index) => {
                            return (
                                <div key={index}>
                                    <Cell
                                        size="l"
                                        description={friend.status || ''}
                                        before={<Avatar src={friend.photo_200 || friend.photo_200_orig}/>}
                                        onClick={() => window.open(`https://vk.com/id${friend.id}`)}

                                        bottomContent={
                                            friend.last_seen ?
                                                moment(friend.last_seen.time).format('DD MMM YYYY, HH:mm:ss'):'неизвестно'
                                        }
                                    >

                                        {`${friend.first_name} ${friend.last_name}`}
                                    </Cell>
                                </div>
                            );
                        })
                    }
                </Group>
            </Panel>
        );
    }
}

VueFriendsLastActivity.propTypes = {
    go: PropTypes.func.isRequired,
    access_token: PropTypes.string.isRequired
};

export default VueFriendsLastActivity;
